import {useEffect, useState } from "react";
import axios from "axios";
import moment from "moment";


// bi v ang vit 1 customhook nn 
// mt ci customhook cn phi bt u vi keyword use
// khi c keyword use th react mi hiu y l customhook
const useFetch = (url, isCovidData) => {
    const [ data, setData] = useState ([]);

    const [isLoading, setIsLoading] = useState (true);

    const [isError, setIsError] = useState (false);
    // = vi hm componentDidMount
    useEffect ( () => {
        const ourRequest = axios.CancelToken.source()

        async function fetchData() {
        // handle li s dng try/catch(e)
            try {
                
                // setTimeout(async() => {
                let res = await axios.get(url, {
                    cancelToken: ourRequest.token, // <-- 2nd step
                    })
                    
                // du ? dng  tr v true/false
                let data = res && res.data ? res.data : []; 
                // gii thch code dng trn:
                // nu iu kin (res && res.data) ng (c gi tr) th gn bin data = res.data
                // cn nu iu kin (res && res.data) (khng c gi tr) sai th gn bin data = []


                // nu khng dng du ? th c th vit theo kiu truyn thng bng if/else
                // let data = null;
                // if (res && res.data) {
                //     data = res.data;
                // } else {
                //     data =[]
                // }
            
                if(data && data.length > 0 && isCovidData === true) {
                    data.map(item => {
                        item.Date = moment(item.Date).format('DD/MM/YYYY');
                        return item;
                    })

                    data = data.reverse()
                    // .reverse() dng  hin th ngc d liu 
                }

                setData(data);
                setIsLoading(false);
                setIsError(false);
                //  y c 3 ln set li State nn component render li 3 ln
            }

                // fetchData();

            // }, 3000)
            catch(err) {
                if (axios.isCancel(err)) {
                    console.log('Request canceled', err.message);
                  } else {
                    setIsError(true);
                    setIsLoading(false);
                }
                
            }
        }

        setTimeout(() => {
            fetchData();
        },2000);

        return () => {
            ourRequest.cancel('Operation canceled by the user.') // <-- 3rd step
        }
        
    },[url]);

    return {
        data, isLoading, isError
    }

}

export  default useFetch;